/* change dimensions at different break points */
.image-selection {
  max-height: 100px;
  max-width: 100%;
  margin: 0 auto;
  opacity: 0.75;
}

.image-wrapper {
  position: relative;
}

.delete-img {
  display: none;
  position: absolute;
  top: 2px;
  right: 2px;
  z-index: 1;
  cursor: pointer;
}
/* show X when enters image div */
.image-wrapper:hover .delete-img {
  display: block;
}

.images-container {
  overflow: auto;
  max-height: 325px;
}

.image-selection:hover {
  opacity: 1;
}

.sample-img {
  max-height: 480px;
  margin: 0 auto;
}

.image-container {
  margin-bottom: 2rem;
}

@media only screen and (max-width: 992px) {
  .image-selection {
    max-width: 120px;
  }
  .images-container {
    display: inline-flex;
    height: auto;
    width: 520px;
    overflow-x: scroll;
  }

  .image-modal-container {
